@import '~assets/styles/h3Imports';

.root {
  --icon-fill: var(--gray-2);

  p {
    @include paragraph;

    color: var(--gray-6, #404040);
    margin-bottom: 24px;
  }

  button:disabled {
    --icon-fill: var(--gray-4);
  }

  input {
    --min-width-input: 266px;
    --input-disabled-background: var(--gray-0, #f1f3f3);

    @media (max-width: 915px) {
      width: 100%;
    }
  }

  p > span {
    color: var(--interface-error, #e10900);
  }

  .wraper {
    display: grid;
    grid-template-columns: 50% 50%;
    width: 100%;

    @media #{$large-down} {
      [data-fontsize='AAA'] & {
        display: block;

        .input {
          padding-right: 0;
        }

        [data-element='PhoneNumber'] {
          padding: 1.5em 1em;
        }
      }
    }

    @media #{$tablet-down} {
      [data-fontsize='AAA'] & {
        [data-element='PhoneNumber'] {
          padding: 1.5em 0.5em;
        }
      }
    }

    > div:last-child .input {
      padding-right: 0;
    }

    .input {
      min-height: rem(88);
      padding: 0 40px 16px 0;
      @media #{$tablet-up} {
        [data-fontsize='AA'] & {
          padding-right: 20px;
        }
      }

      input {
        color: var(--gray-4, #414646);
        font-weight: 600;
        height: rem(50);
        width: 100%;
      }
    }

    > div > div {
      height: auto;

      &[data-validity='invalid'] {
        height: auto;
      }
    }

    @media #{$small-down} {
      display: inline-block;
      width: 100%;

      .input {
        padding: 0 0 16px;
      }
    }
  }
}
